<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process271"></a>

<p><br></p>
<p><span class="rvts17">博学者（</span><span class="rvts50">The Loremaster</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:386,,Y:321</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：克兰莉</span><span class="rvts20"></span><span class="rvts21">哈波特（</span><span class="rvts20">Cranley Huwbert</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">通过克兰莉</span><span class="rvts20"></span><span class="rvts21">哈波特的对话，并答对所有问题</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1492.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在阿克斯的城东区，可以找到博学者克兰莉，与他对话，他会问你一系列的《神界》历史问题，全部答对后，他会给你意外的惊喜。</span>
<br><span class="rvts21">克兰莉的问题一共有</span><span class="rvts20">4</span><span class="rvts21">道题，答案分别是：</span>
<br><span class="rvts20">1. </span><span class="rvts21">梦想之屋（</span><span class="rvts20">House of Dream</span><span class="rvts21">）</span><span class="rvts20"></span>
<br><span class="rvts20">2. ?233</span><span class="rvts21">年</span><span class="rvts20"></span>
<br><span class="rvts20">3. </span><span class="rvts21">塔纳科斯（</span><span class="rvts20">Tenax</span><span class="rvts21">）</span><span class="rvts20"></span>
<br><span class="rvts20">4. </span><span class="rvts21">卡珊卓（</span><span class="rvts20">Cassandra</span><span class="rvts21">）</span><span class="rvts20"></span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1493.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">通过测试后，他会把壁炉旁的隐藏地下室打开。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1494.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在博学者克兰莉的藏书室中，有很多独一无二的书，并且这里也藏有很多的高等级技能书。再次与他对话，他会问你一个比较模糊的问题，无论你选择什么，他都会奖励你一本源力技能书。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



